<template>
  <div class="page-edm">
    <div class="btn-container">
      <el-button type="primary">
        H5<i class="el-icon-view el-icon--right"></i>
      </el-button>
      <el-button @click="spawnLink" type="primary">
        <i class="el-icon-upload el-icon--right"></i>
      </el-button>
    </div>
    <el-form label-position="right" label-width="100px">
      <div class="item-group">
        <el-form-item label="H5">
          <el-input v-model="h5Name"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="">
          <div class="item-content">
            <label></label>
            <el-input type="textarea" v-model="spBackdrop"
              :disabled="disPos"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input type="textarea" v-model="positionRequired"
              :disabled="disPos"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input v-model="address"
              :disabled="disPos"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editPos" type="primary">
          {{btnPosText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="">
          <div class="item-content">
            <label></label>
            <el-input v-model="team"
              :disabled="disCom" 
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input v-model="smartPeople"
              :disabled="disCom"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input v-model="extendSpeed"
              :disabled="disCom"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editCom" type="primary">
          {{btnComText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="">
          <div class="item-content">
            <label></label>
            <el-input v-model="positionUpgrade"
              :disabled="disEmp"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input v-model="salaryInc"
              :disabled="disEmp"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input type="textarea" v-model="huntTarget"
              :disabled="disEmp"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editEmp" type="primary">
          {{btnEmpText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        h5Name: 'EDM',
        company: '',
        content: 'AHR',
        team: '',
        positionRequired: '',
        address: '243784',
        smartPeople: '',
        spBackdrop: '23&',
        extendSpeed: '10%',
        positionUpgrade: '1.7',
        salaryInc: '17%',
        huntTarget: '60%',
        disPos: true,
        disCom: true,
        disEmp: true
      }
    },
    computed: {
      btnPosText(){
        return this.disPos ? '' : ''
      },
      btnComText(){
        return this.disCom ? '' : ''
      },
      btnEmpText(){
        return this.disEmp ? '' : ''
      }
    },
    methods: {
      editPos(){
        this.disPos = !this.disPos
      },
      editCom(){
        this.disCom = !this.disCom
      },
      editEmp(){
        this.disEmp = !this.disEmp
      },
      spawnLink(){
        this.$prompt('H5', '', {
          confirmButtonText: '',
          showCancelButton: false,
          closeOnClickModal: false,
          inputValue: 'http://uimg.ifchange.com/special/1/195/index.html'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: ''
          })
          console.log(value)
        })
      }
    }
  }
</script>

<style lang="stylus">
  @import './Edm.styl'
</style>


