<template>
  <div class="hello">
    <el-button @click="showMessage"></el-button>
    <el-button @click="showMessageBox"></el-button>
    <el-button @click="showNotification"></el-button>
    <el-select v-model="value" placeholder="">
      <el-option v-for="(item, index) in options"
        :label="item.label"
        :value="item.value"
        :key="index">
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data() {
    return {
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: ''
    }
  },
  methods: {
    showMessage(){
      this.$notify({
        title: '',
        message: '',
        type: 'success'
      })
    },
    showMessageBox(){
      this.$confirm('confirm')
    },
    showNotification(){
      this.$alert('alert')
    }
  }
}
</script>



