<template>
  <div class="page-edm">
    <div class="btn-container">
      <el-button type="primary">
        <i class="el-icon-view el-icon--right"></i>
      </el-button>
      <el-button @click="goEditH5" type="primary">
        H5<i class="el-icon-edit el-icon--right"></i>
      </el-button>
    </div>
    <el-form label-position="right" label-width="100px">
      <div class="item-group">
        <el-form-item label="EDM">
          <el-input :disabled="disPos" 
            v-model="templateName" 
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :disabled="disPos" 
            v-model="company"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :disabled="disPos" 
            v-model="sendTo"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :disabled="disPos" 
            v-model="positionName"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="">
          <div class="item-content">
            <label></label>
            <el-input :disabled="disPos" type="textarea" 
              v-model="positionDesc"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input :disabled="disPos" type="textarea" 
              v-model="positionRequired"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input :disabled="disPos" 
              v-model="address"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editPos" type="primary">
          {{btnPosText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="">
          <div class="item-content">
            <label></label>
            <el-input :disabled="disCom" 
              v-model="team"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input :disabled="disCom" 
              v-model="smartPeople"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input :disabled="disCom" 
              v-model="extendSpeed"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editCom" type="primary">
          {{btnComText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="">
          <div class="item-content">
            <label></label>
            <el-input :disabled="disEmp" 
              v-model="positionUpgrade"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input :disabled="disEmp" 
              v-model="salaryInc"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label></label>
            <el-input :disabled="disEmp" type="textarea" 
              v-model="huntTarget"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editEmp" type="primary">
          {{btnEmpText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        templateName: 'EDM',
        company: '',
        sendTo: 'ID',
        content: 'AHR',
        positionName: '',
        positionDesc: '',
        positionRequired: '',
        address: '243784',
        team: '21%',
        smartPeople: '23&',
        extendSpeed: '10%',
        positionUpgrade: '1.7',
        salaryInc: '17%',
        huntTarget: '60%',
        disPos: true,
        disCom: true,
        disEmp: true
      }
    },
    computed: {
      btnPosText(){
        return this.disPos ? '' : ''
      },
      btnComText(){
        return this.disCom ? '' : ''
      },
      btnEmpText(){
        return this.disEmp ? '' : ''
      }
    },
    methods: {
      goEditH5(){
        this.$router.push({ name: 'edm-h5' }) 
      },
      editPos(){
        this.disPos = !this.disPos
      },
      editCom(){
        this.disCom = !this.disCom
      },
      editEmp(){
        this.disEmp = !this.disEmp
      }
    }
  }
</script>

<style lang="stylus">
  @import './Edm.styl'
</style>


